<template>
<div class="seller" ref="sellerHome">
	<transition name="fade">	
	<section ref="topHeader" v-show="shopTopHeader">
		<header class="header pr">
			<img class="top" src="https://fuss10.elemecdn.com/c/e0/283549d417057f6829e829af1a2eapng.png" alt="">
			<button class="back" @click="$router.go(-1)"><i class="iconfont icon-fanhui1"></i></button>
			<button class="search"><i class="iconfont icon-sousuoleimu"></i></button>
			<button class="share"><i class="iconfont icon-skip1"></i></button>
			<button class="collect"><i class="iconfont icon-xihuanfill"></i></button>
			<span class="logo"><img :src="shop.seller.logo" alt="" /><i v-if="shop.seller.tag">{{shop.seller.tag}}</i></span>
		</header>
		<section class="baseInfo pt50">
			<p class="name tc f18 mb10">{{shop.seller.name}}</p>
			<p class="tc">
				<span v-if="shop.seller.score">评价{{shop.seller.score}}</span>
				<span v-if="shop.seller.sales" class="ml10 mr10">月售{{shop.seller.sales}}</span>
				<span v-if="shop.seller.hummingbird">{{shop.seller.hummingbird}}约{{shop.seller.duration}}分钟</span>
			</p>
			<button class="adoptive" @click="switchInstruction" v-if="!showInstruction">
				<div class="w oh huodong">
					<div class="fl" v-if="shop.seller.offer[0].list.length>0">
						<i class="youh">{{shop.seller.offer[0].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[0].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[1].list.length>0">
						<i class="youh">{{shop.seller.offer[1].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[1].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[2].list.length>0">
						<i class="shoudan">{{shop.seller.offer[2].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[2].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[3].list.length>0">
						<i class="youh">{{shop.seller.offer[3].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[3].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[4].list.length>0">
						<i class="youh">{{shop.seller.offer[4].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[4].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[5].list.length>0">
						<i class="shoudan">{{shop.seller.offer[5].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[5].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[6].list.length>0">
						<i class="huiyuan">{{shop.seller.offer[6].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[6].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fl" v-else-if="shop.seller.offer[7].list.length>0">
						<i class="youh">{{shop.seller.offer[7].name}}</i>
						<span v-for="(sol,index) in shop.seller.offer[7].list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fr">{{getYouHuiCount(shop.seller.offer)}}个优惠<i class="iconfont icon-xiangxia2"></i></div>
				</div>
				<div class="gonggao" v-if="shop.seller.announcement">公告：{{shop.seller.announcement}}</div>
			</button>
		</section>
		<section class="instruction" v-show="showInstruction">
			<dl>
				<dt>优惠 <button class="shouqi" @click="switchInstruction"><i class="iconfont icon-xiangshang2"></i></button></dt>
				<dd class="youhui">
					<p v-for="(item,index) in shop.seller.offer" v-if="item.list.length>0" :key="index">
						<i :class="youhuoStyle(item.name)">{{item.name}}</i>
						<span v-for="(subItem,index) in item.list" :key="index">{{subItem.name}}</span>
					</p>
				</dd>
			</dl>
			<dl v-if="shop.seller.service.length>0">
				<dt>服务</dt>
				<dd class="fuwu">
					<p v-for="(s,index) in shop.seller.service" :key="index">
						<i>{{s.name}}</i>
						<span>{{s.desc}}</span>
					</p>
				</dd>
			</dl>
			<dl v-if="shop.seller.announcement">
				<dt>公告</dt>
				<dd class="gonggao">{{shop.seller.announcement}}</dd>
			</dl>
		</section>
	</section>
	</transition>	


	<section class="shopContent">
		<header class="tc pt10 pb10 shopHeader" :class="shopHeaderFixed" ref="shopHeader">
			<button :class="shopHeader[0]" @click="switchShopHeader(0)">点餐</button>
			<button :class="shopHeader[1]" @click="switchShopHeader(1)">评价<span class="count">1380</span></button>
			<button :class="shopHeader[2]" @click="switchShopHeader(2)">商家<span class="story">有故事</span></button>
		</header>

		<swiper :options="swiperOption" class="category" ref="swiperOption">
		    <swiper-slide>
		    	<transition name="fade">	
		    	<section ref="prevFood" v-show="showPrevFood">
					<swiper :options="swiperShopHeaderBannerOption" class="swiperShopHeaderBannerOption">
			    		<swiper-slide v-for="(topic,index) in shop.foods[0].list" :key="index">	
			    			<img :src="topic.icon" alt="" />
						</swiper-slide>
					</swiper>	
					<div class="shopRecommendTitle">商家推荐</div>	
					<swiper :options="swiperShopTuiJianOption" class="swiperShopTuiJianOption">
			    		<swiper-slide v-for="(shopRecommend,index) in getGoodsList(shop.foods[1].list)" :key="index">
							<button class="item">
								<img :src="shopRecommend.icon" alt="" />
								<p class="name">{{shopRecommend.name}}</p>
								<p class="info">
									<span>月售{{shopRecommend.sales}}</span>
									<span v-if="shopRecommend.applauseRate>0">好评{{shopRecommend.applauseRate}}%</span>
								</p>
								<div class="foot">
									<span class="price">{{shopRecommend.price}}</span>
									<span class="mark"></span>
									<div class="buy">
										<button class="less"><i class="iconfont icon-shanchu6"></i></button>
										<span>1</span>
										<button class="add"><i class="iconfont icon-jiahao2fill"></i></button>
									</div>
								</div>
							</button>
			    		</swiper-slide>
					</swiper>
				</section>	
				</transition>
				<div class="foods" :class="foodsFixed" ref="foods">
					<div class="left">
						<button v-for="(category,index) in getCategoryName(shop.foods)" :key="index" :class="categoryNameStyle">
							<i></i>
							<span>{{category.name}}</span>
							<em></em>
						</button>
					</div>
					<div class="right">
						<section v-for="category in getCategoryName(shop.foods)">
							<div class="head">{{category.name}} <span>{{category.desc}}</span></div>
							<button v-for="food in getGoodsList(category.list)" class="food">
								<img :src="food.icon" alt="" />
								<div class="con">
									<p class="name">{{food.name}}</p>
									<p class="desc">{{food.desc}}</p>
									<p class="info">
										<span>月售{{food.sales}}</span>
										<span v-if="food.applauseRate>0">好评{{food.applauseRate}}%</span>
									</p>
									<div class="foot">
										<span class="price">{{food.price}}</span>
										<div class="buy">
											<button class="less"><i class="iconfont icon-shanchu6"></i></button>
											<span>1</span>
											<button class="add"><i class="iconfont icon-jiahao2fill"></i></button>
										</div>
									</div>
								</div>
							</button>
						</section>
					</div>
				</div>
		    </swiper-slide>
		    <swiper-slide>
				评价
		    </swiper-slide>
		    <swiper-slide>
				商家
		    </swiper-slide>
		</swiper>		
	</section>

</div>
</template>

<script>

export default {
	name: 'seller',
	components: {},
	data () {
		var _this=this;
		return {
			msg: 'hello',
			showInstruction:false,
			sellers:require("../seller.json"),
			shopId: this.$route.params.id,
			shop:{},
			swiperOption:{ //点餐 评价 商家 大类的滑动
				on: {
			    	slideChangeTransitionEnd: function(){
			      		_this.switchShopHeaderLight(this.activeIndex);
			    	}
			  	}
			},
			swiperShopHeaderBannerOption:{}, //点餐 专题滑动
			swiperShopTuiJianOption:{ //商家推荐的滑动特效
				slidesPerView: 3,
      			spaceBetween: 5,
			},
			shopHeader:['on','',''],
			categoryNameStyle:[],
			topHeaderHeight:0, //点餐 评价 商家 标题以上所有内容的高度
			shopHeaderFixed:'',
			shopHeaderHeight:0, //点餐 评价 商家 标题的内容高度
			prevFoodHeight:0, //点餐类目下 food以上的内容高度
			foodsFixed:'',
			shopTopHeader:true,
			showPrevFood:true
		}
	},
	watch: {
		
	},
	created(){
		this.sellers.forEach(i=>{
			i.seller.id===this.shopId ? this.shop=i : "";
		})
	},
	mounted(){
		this.topHeaderHeight=this.$refs.topHeader.clientHeight;
		this.shopHeaderHeight=this.$refs.shopHeader.clientHeight; 
		this.prevFoodHeight=this.$refs.prevFood.clientHeight; 
		window.onscroll = e => {
			var e =e || window.event;
			var scrolltop=document.documentElement.scrollTop||document.body.scrollTop; //滚动高度
  			if(this.$route.name==="sellerHome"){
  				if(scrolltop>=this.topHeaderHeight){ console.log(1);
  					this.shopHeaderFixed="fixed";
  					//this.shopTopHeader=false;
  					this.$refs.sellerHome.style.paddingTop=this.shopHeaderHeight+"px";
  					

  					if(this.shopHeader[0]==='on'){
	  					if(scrolltop>=(this.topHeaderHeight+this.prevFoodHeight)){console.log(2);
	  						this.foodsFixed='fixed';
	  						this.$refs.sellerHome.style.height="100%";

	  						
	  						//this.$refs.topHeader.style.display="none";
	  					
	  						//this.showPrevFood=false;
	  						//this.$refs.foods.style.height=document.documentElement.offsetHeight-this.shopHeaderHeight+"px";
	  						//this.shopHeaderFixed="fixed";
	  						
	  					}else{console.log(3);
	  						this.foodsFixed='';
	  					
	  						//this.$refs.sellerHome.style.height="auto";
	  						//this.$refs.topHeader.style.display="block";
	  						//this.showPrevFood=true;
	  						//this.$refs.foods.style.height="auto";
	  					}
	  				}else{

	  				}


  				}else{console.log(4);
  					this.shopHeaderFixed="";
  					this.$refs.sellerHome.style.paddingTop="0px";
  					




  					if(this.shopHeader[0]==='on'){
	  					if(scrolltop>=(this.topHeaderHeight+this.prevFoodHeight)){console.log(5);
	  						//this.$refs.sellerHome.style.height="100%";
	  						//this.$refs.topHeader.style.display="none";
	  						this.showPrevFood=false;
	  						//this.$refs.foods.style.height=document.documentElement.offsetHeight-this.shopHeaderHeight+"px";
	  						//this.shopHeaderFixed="fixed";
	  						//this.foodsFixed='fixed';
	  					}else{console.log(6);
	  						//this.foodsFixed='';
	  						//this.$refs.topHeader.style.display="block";
	  						//this.showPrevFood=true;
	  						//this.$refs.foods.style.height="auto";
	  					}
	  				}else{
	  					
	  				}




  				}
  				
  			}
		};
	},
	computed: {
	
	},
	methods: {
		//分别显示优惠样式
		youhuoStyle(i){
			let s="";
			switch(i){
				case "满减" :
				case "折扣" :
				case "特价" :
				case "红包" : s="youh"; break;
				case "新客" :
				case "首单" : s="shoudan"; break;
				case "会员" : s="huiyuan"; break;
			}
			return s;
		},
		// 切换显示优惠明细
		switchInstruction () {
			this.showInstruction = !this.showInstruction;
		},
		//返回优惠数量
		getYouHuiCount (objList) {
			let n=0;
			objList.forEach(item=>{
				item.list.length>0 ? n++ : "";
			})
			return n;
		},
		//切换 电池  评价 商家
		switchShopHeader (i) {
			this.switchShopHeaderLight(i);
			this.$refs.swiperOption.swiper.slideTo(i, 300, false);
		},
		//高亮 电池  评价 商家
		switchShopHeaderLight (i) {
			this.shopHeader=[];
			this.shopHeader[i]='on';
		},
		//返回商品列表 商家推荐
		getGoodsList (ids) {
			var goods=[];
			var list=this.shop.goods;
			ids.forEach(id=>{
				list.forEach(item=>{
					id===item.id ? goods.push(item) : "";
				})
			})
			return goods;
		},
		//获取商品左侧类目名称时，从 “热销” 开始
		getCategoryName (list) {
			let arr=[];
			list.forEach((item,index)=>{
				index>1 ? arr.push(item) : ""
			})
			return arr;
		},
		//返回商家页 点餐类目下的商品列表信息
		getFoods (list) {
			let arr=[];
			let newList=[];
			list.forEach((item,index)=>{
				index>1 ? newList.push(item) : ""
			})
			return arr;
		}
	}
}
</script>

<style scoped lang="less">
button{background-color:transparent;border:none;outline:none;}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.seller{background-color:#fff;padding-bottom: 50px;
	.header button{position:absolute;top:10px;z-index:2;color:#fff;}
	.header button:hover{cursor:pointer;}
	.header .iconfont{font-size:1.5em;}
	.back .iconfont{left: 10px;font-size: 1.2em;}
	.share{right: 10px;}
	.search{right: 50px;}
	img.top{display: block;width: 100%;height: auto;}
	.header .collect{background-color: #fff;width: 30px;height: 30px;text-align: center;line-height: 30px;display:block;border-radius: 100%;top:auto;bottom: -15px;right: 20px;padding:0;box-shadow: 0px 3px 3px #ddd;
		i{color:#f55050;position: relative;top:3px;}
	}
	.logo{width: 100px;display:block;position:absolute;left: 50%;border-radius: 3px;margin-left: -50px;overflow:hidden;bottom: -25px;
		img{display:block;width: 100%;height: auto;border-radius: 3px;}
		i{font-style: normal;background-color: yellow;position: absolute;top:0;left: 0;z-index: 2;padding:0px 3px;border-radius: 3px 0 3px 0;}
	}
	.adoptive{width:100%;display:block;padding:15px 10px 5px;box-sizing: border-box;
		.huodong{height:30px;
			.fl{color: #666;
				i{font-style: normal;border-width:1px;border-style:solid;padding:1px 5px;border-radius: 3px;box-sizing: border-box;}
				.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
				.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
				.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}
				span{margin-left: 5px}
			}
			.fr{font-size: .8em;color: #999;
				i{font-size: .8em;}
			}
		}
		.gonggao{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;color: #999;}	
	}
	.instruction{padding: 20px 10px;box-sizing: border-box;margin-bottom:50px;transition: all 0.3s;
		i{font-style: normal;}
		dl{margin-bottom: 10px;}
		dt{font-weight: bolder;height: 40px;line-height: 40px;color: #333;position: relative;
			.shouqi{position: absolute;right: 0;top: 0;z-index: 2;}
		}
		dd{font-size: .8em;color: #666;}
		.youhui{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border-width:1px;border-style:solid;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}
			.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
			.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
			.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}

		}
		.fuwu{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border:1px solid #ddd;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}	
			span{word-wrap:break-word;word-break:break-all;line-height: 20px;display:table;}
		}
	}
	.swiperShopHeaderBannerOption{margin:0 10px;
		img{width:100%;display:block;height: auto;}
	}
	.shopRecommendTitle{margin:0 10px;padding:20px 0 5px;}
	.swiperShopTuiJianOption{margin:0 10px;
		.name{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;text-align: left;}
		.info{font-size: .8em;color:#999;text-align: left;
			span:first-child{margin-right:5px;}
		}
		.swiper-slide{box-shadow:0px 0px 2px #eee;box-sizing: border-box;}
		button.item{width:100%;display:block;
			p{margin-bottom: 5px;}
			img{width:100%;display:block;height: auto;}
			.foot{width:100%;display:flex;
				.price{width:50px;color: #ff5339;
					&:before{content:"￥";font-size: .8em;}
				}
				.buy{min-width:90px;float: right;text-align: right;
					span{font-size: .8em;position: relative;top:-2px;}
					button{
						i{color:#2396ff;}
					}
				}
			}
		}
	}
	.shopContent{height: 100%;
		.category{height: 100%;}
		.shopHeader{width:100%;max-width:640px;overflow:hidden;
			&.fixed{position: fixed;top:0;left: 50%;transform: translate(-50%,0);background-color: #f5f5f5;z-index: 10;}
			button{width:33.33%;float: left;position: relative;
				span{position: absolute;top:2px;font-size: .8em;}
				.count{color:#999;padding-left: 5px;}
				.story{background-color:#DFF2FF;color:#2395ff;padding:1px 4px 2px;border-radius: 5px;margin-left: 5px;}
				&.on{color: #2395ff;}
			}
		}
	}
	.foods{padding-top:10px;display:flex;
		&.fixed{position: fixed;left: 50%;top:0;z-index: 10;width:100%;max-width: 640px;transform: translate(-50%,0);
			.left{height:100%;}
			.right{height:100%;overflow-y:auto;}
		}
		.left{width:100px;background-color: #F8F8F8;
			button{
				display:block;width: 100%;height: 50px;line-height: 50px;position: relative;
				span{color: #666;display:block;text-align: left;padding-left: 10%;}
			}
		}
		.right{width:calc(100% - 100px);padding:0 0 50px 10px;background-color: #fff;
			section{margin-bottom: 20px;}
			.head{
				span{color: #999;font-size: .8em;}
			}
			.food{width:100%;display:flex;padding:5px 0;
				img{width:25%;display:inline-block;}
				.con{width: 75%;display:inline-block;padding-left: 10px;box-sizing:border-box;
					p{margin-bottom: 3px;}
					.name,.desc,.info{text-align: left;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
					.desc,.info{color:#999;font-size: .8em;}
					.foot{width:100%;overflow:hidden;}
					.price{float:left;color: #ff5339;
						&:before{content:"￥";font-size: .8em;}
					}
					.buy{min-width:90px;float: right;text-align: right;
						span{font-size: .8em;position: relative;top:-2px;}
						button{
							i{color:#2396ff;}
						}
					}
				}
			}
			
		}
	}
}
</style>


